import { PATCH } from "../UrlConfig/MethodType";

export const classTabManageFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v1/class/tab/manage/${data?.did}/query?flow=${data?.flow ?? ""}`,
    providesTags: ["Tab_Manage"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const classTabControllerFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/class/tab/manage/${data?.did}/query`,
      method: PATCH,
      body: data.tabManage,
    }),
    invalidatesTags: ["Tab_Manage"],
  });
};

export const catalogClassAllStudentListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v1/ins/${data.id}/unapprove-student/list/combined/query?page=${data.page}&limit=${data.limit}&search=${data?.search}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.studentIns,
        };
      else
        return {
          page: arg?.page,
          list: response?.studentIns,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },

    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const classCatalogReshuffleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/feed/filter/by/trigger/class/catalog/${data?.cid}?sort_query=${data?.sort_query}`,
      method: PATCH,
      body: data.bodyParameter,
    }),
  });
};
