import { classManagerApi } from "./classManagerApi";
import {
  classTabControllerFunction,
  classTabManageFunction,
  catalogClassAllStudentListFunction,
  classCatalogReshuffleFunction,
} from "./class_manager_query_mutation_function";

const classManagerApiDynamic = classManagerApi.injectEndpoints({
  endpoints: (builder) => ({
    classTabManage: classTabManageFunction(builder),
    classTabController: classTabControllerFunction(builder),
    catalogClassAllStudentList: catalogClassAllStudentListFunction(builder),
    classCatalogReshuffle: classCatalogReshuffleFunction(builder),
  }),
});
export const {
  useClassTabManageQuery,
  useClassTabControllerMutation,
  useCatalogClassAllStudentListQuery,
  useClassCatalogReshuffleMutation,
} = classManagerApiDynamic;
