import { useState } from "react";
import { useTranslation } from "react-i18next";
import ModalMenu from "../../Ui/Modal/ModalMenu";
import TileWrapper from "../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../Ui/Tab/Tile/TileCard";
import { useClassCatalogReshuffle } from "../../Hooks/ApiHooks/ClassManager/class-manager-api";
import ClassCatalogResuffleList from "../../UserStaffMember/ClsManager/ClassCatalog/ClassCatalogResuffleList";

const ClassReshuffleMenu = ({ onClose, cid, onRefetch, students }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [state, setState] = useState("");
  const [isAlpha, setIsAlpha] = useState(false);

  const [classCatalogReshuffle] = useClassCatalogReshuffle();

  const onStepToggle = () => {
    setIsAlpha((pre) => !pre);
  };
  const onAction = (val) => {
    if (cid) {
      if (val === "Alpha" || val === "Alpha_Last") {
        setState(val);
      }
      setDisabled((pre) => !pre);
      classCatalogReshuffle({
        cid: cid,
        sort_query: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      {step === 1 ? (
        <ClassCatalogResuffleList
          cid={cid}
          onRefetch={onRefetch}
          onClose={() => setStep("")}
          onParentClose={onClose}
          students={students}
        />
      ) : (
        <ModalMenu
          onClose={onClose}
          disabled={disabled}
          customStyle={{
            width: "18rem",
          }}
        >
          {/* <h6 onClick={() => onAction("Alpha")}>{t("alphabetical")}</h6> */}
          <h6 onClick={onStepToggle}>{t("alphabetical")}</h6>
          {isAlpha ? (
            <div>
              <TileWrapper
                customTileContainer={{
                  marginBottom: "0.7rem",
                }}
              >
                {admissionIntakeType?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onAction}
                    activeTile={state}
                  />
                ))}
              </TileWrapper>
            </div>
          ) : null}
          <h6 onClick={() => onAction("Gender")}>{t("gender")}</h6>
          <h6 onClick={() => onAction("Gender_Alpha")}>{t("both")}</h6>
          <h6 onClick={() => setStep(1)}>{t("using_manual_optional")}</h6>
        </ModalMenu>
      )}
    </>
  );
};
export default ClassReshuffleMenu;

export const admissionIntakeType = [
  {
    key: "Alpha",
    name: "First Name",
  },
  {
    key: "Alpha_Last",
    name: "Surname",
  },
];
