import {
  useClassTabControllerMutation,
  useClassTabManageQuery,
  useCatalogClassAllStudentListQuery,
  useClassCatalogReshuffleMutation,
} from "../../../Services/ClassManager/classManagerApiDynamic";

export const useClassTabManage = (args) => {
  const { data, refetch, isFetching } = useClassTabManageQuery(args.data, {
    skip: args.skip,
  });
  return {
    classTabManage: data,
    classTabManageRefetch: refetch,
    classTabManageLoading: isFetching,
  };
};

export const useClassTabController = () => {
  const [classTabController] = useClassTabControllerMutation();
  return [classTabController];
};
export const useCatalogClassAllStudentList = (args) => {
  const { data, refetch, isFetching } = useCatalogClassAllStudentListQuery(
    args?.data,
    {
      skip: args.skip,
    }
  );
  return {
    catalogClassAllStudentList: data,
    catalogClassAllStudentListRefetch: refetch,
    catalogClassAllStudentListLoading: isFetching,
  };
};

export const useClassCatalogReshuffle = () => {
  const [classCatalogReshuffle] = useClassCatalogReshuffleMutation();
  return [classCatalogReshuffle];
};
