import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
const ClassCatalogResuffleListCard = ({ student }) => {
  const { t } = useTranslation();

  return (
    <div className={style.sf_card_hover}>
      <div className={style.sf_card_contatiner}>
        <img
          src={
            student?.studentProfilePhoto
              ? `${imageShowUrl}/${student.studentProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          loading="lazy"
          alt="avatar"
        />

        <div className={style.sf_card_contatiner_inner}>
          <h6>
            {`${student?.studentFirstName ?? ""} ${
              student?.studentMiddleName ?? ""
            } ${student?.studentLastName ?? ""}`}
          </h6>
          <p>
            {t("gr_number")} {": "}
            <span
              style={{
                fontWeight: "500",
              }}
            >
              {student?.studentGRNO ?? ""}
            </span>
            {student?.student_prn_enroll_number ? (
              <span
                style={{
                  fontWeight: "500",
                }}
              >
                {`, ${t("enrollment_number_only")}: ${
                  student?.student_prn_enroll_number
                }`}
              </span>
            ) : null}
          </p>
        </div>
      </div>
    </div>
  );
};

export default ClassCatalogResuffleListCard;
