import { useEffect, useRef, useState } from "react";
import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/Income/Income.module.css";
import { useClassResuffleRearrange } from "../../../../hooks/member_tab/class-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import ClassCatalogResuffleListCard from "./ClassCatalogResuffleListCard";
const ClassCatalogResuffleList = ({
  cid,
  onClose,
  onParentClose,
  students,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [list, setList] = useState([]);
  const dragItem = useRef();
  const dragOverItem = useRef();

  useEffect(() => {
    if (students?.length > 0) {
      setList(students);
    }
  }, [students]);
  const [classResuffleRearrange] = useClassResuffleRearrange();

  const onDragEventStart = (e, position) => {
    dragItem.current = position;
  };

  const onDragEventEnter = (e, position) => {
    dragOverItem.current = position;
  };
  const onDropItem = (e) => {
    const listCopy = [...list];
    const dragItemContentCls = listCopy[dragItem.current];
    listCopy.splice(dragItem.current, 1);
    listCopy.splice(dragOverItem.current, 0, dragItemContentCls);

    dragItem.current = null;
    dragOverItem.current = null;
    setList(listCopy);
  };
  const onReshuffle = () => {
    if (cid) {
      setDisabled((pre) => !pre);
      let arr = [];
      for (let ids of list) {
        arr.push(ids?._id);
      }
      classResuffleRearrange({
        suffle: {
          shuffle_arr: arr,
          cid: cid,
          flow: "CLASS_WISE",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onParentClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <Modal onClose={() => {}}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("reshuffle_student_list")}</h6>
              </div>
              <div className={style.modal_container_header}>
                <CreateButton
                  label={t("save_resuffle")}
                  onAction={onReshuffle}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            {list?.map((student, index) => (
              <div
                key={student?._id}
                draggable
                onDragStart={(e) => onDragEventStart(e, index)}
                onDragEnter={(e) => onDragEventEnter(e, index)}
                onDragEnd={onDropItem}
              >
                <ClassCatalogResuffleListCard student={student} />
              </div>
            ))}
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default ClassCatalogResuffleList;
